import {
  AfterContentInit,
  AfterViewChecked,
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  ActivatedRoute,
  NavigationStart,
  Router,
  ROUTES,
} from '@angular/router';
import { ScullyRoute, ScullyRoutesService } from '@scullyio/ng-lib';
import { Observable, fromEvent, Subject } from 'rxjs';
import { tap, map, switchMap, takeUntil, filter } from 'rxjs/operators';
import { HighlightService } from '../shared/services/highlight.service';
import { BlogService } from './blog.service';

declare var ng: any;

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.scss'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.Emulated,
})
export class BlogComponent
  implements OnInit, AfterContentInit, AfterViewChecked, OnDestroy {
  @ViewChild('scully') scullyElement: any;

  blog$: Observable<ScullyRoute> = this.scullyRoutesService.getCurrent();

  allowHighlight = true;

  private destroy$ = new Subject();
  constructor(
    private highlightService: HighlightService,
    private scullyRoutesService: ScullyRoutesService
  ) {}

  ngOnInit(): void {
    fromEvent(window, 'AngularReady')
      .pipe(
        takeUntil(this.destroy$),
        tap(() => (this.allowHighlight = false))
      )
      .subscribe();
  }

  ngAfterContentInit(): void {
    // console.log('scullyElement', this.scullyElement);
  }

  ngAfterViewChecked(): void {
    if (this.allowHighlight) {
      this.highlightService.highlightAll();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
